import BoxMenu from "./BoxMenu"
import {Row} from 'react-bootstrap';
import PropTypes from 'prop-types';


const BoxMenuList = ({menuItems}) => {
    return (
        <Row className="setting-container text-center">
            { menuItems.map((m, index)=> <BoxMenu key={index} {...m}></BoxMenu>) } 
        </Row>
    )
}

BoxMenuList.propTypes ={
    menuItems: PropTypes.array
}
export default BoxMenuList;